* {
  margin: 0;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.title {
  font-size: 7vw;
  text-transform: uppercase;
  font-family: "Courier New", Courier, monospace;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 40vh;
  width: 100%;
}

@media (max-width: 900px) {
  .header {
    height: 30vh;
  }
  .title {
    font-size: 11vw;
  }
}

.inputs {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.search {
  width: 100%;
}
.meanings {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: thin;
  height: 50vh;
  border: 10px solid rgb(255, 255, 255);
  border-radius: 10px;
  padding: 10px 20px;
  overflow-x: hidden;
}

.subTitle {
  font-size: 3vw;
  text-align: center;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
}

@media (max-width: 900px) {
  .meanings {
    overflow: scroll;
    overflow-x: hidden;
    height: 60vh;
  }
}
.singleMean {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px 20px;
  margin: 10px 0;
}
